import { Grid, Box, Typography, Button } from "@mui/material";
import React from "react";
import { useHistory } from "react-router";
import { useDispatch } from "react-redux";
import {
  getReplenishmentData,
  setCryptoDialog,
} from "../../../redux/reducers/replenishment";
import arrowRed from "../../../assets/images/arrows/arrowDown.svg";
import arrowGreen from "../../../assets/images/arrows/arrowUp.svg";
import { makeStyles } from "@mui/styles";

const Card = ({ item, t, href, mode, account, setVerificationPush }) => {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();

  return (
    <Box
      sx={{
        background: mode === "light" && "#fff",
        boxShadow: mode === "light" && "0px 2px 0px #E2E8F0",
      }}
      className={classes.div}
    >
      <Grid
        container
        className={classes.grid}
        justifyContent="space-between"
        wrap="nowrap"
      >
        <Grid container className={classes.gridItem} wrap="nowrap">
          <img
            src={item?.logo}
            style={{
              width: 64,
              height: 55,
              objectFit: "contain",
              marginRight: 16,
            }}
            alt=""
          />
          <Box sx={{ overflow: "hidden", maxWidth: "90%" }}>
            <Typography variant="h4" className={classes.prefix}>
              {item?.currency}
            </Typography>
            <Typography variant="body2" className={classes.name}>
              {item?.name}
            </Typography>
          </Box>
        </Grid>
        <Box className={(classes.priceBox, classes.gridItem)}>
          <Typography className={classes.price}>{item?.balance}</Typography>
          <p
            className={item.class}
            variant=""
            style={{
              marginTop: 4,
              whiteSpace: "nowrap",
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-end",
            }}
          >
            <img
              src={
                item?.difference && item.difference[0] === "+"
                  ? arrowGreen
                  : item?.difference && item.difference[0] === "-"
                  ? arrowRed
                  : null
              }
              style={{ marginRight: 6 }}
              alt=""
            />
            <span
              style={{
                fontWeight: "300",
                textOverflow: "ellipsis",
                overflow: "hidden",
                whiteSpace: "nowrap",
                color:
                  item?.difference && item.difference[0] === "+"
                    ? "#10B981"
                    : item?.difference && item.difference[0] === "-"
                    ? "#EF4444"
                    : "#A0ABBB",
              }}
            >
              {item?.difference}
            </span>
          </p>
        </Box>
      </Grid>
      <Box className={classes.buttonBox}>
        <Button
          variant="contained"
          className={classes.button}
          color="primary"
          disabled={!item.can_deposit}
          onClick={() => {
            if (account != 2) setVerificationPush(true);
            else {
              history.push("/dashboard/replenish/cryptocurrency");
              dispatch(setCryptoDialog({ open: true, name: item.name }));
              dispatch(
                getReplenishmentData("cryptocurrency", { method: item.slug })
              );
            }
          }}
          size="large"
        >
          {t("deposit")}
        </Button>
        <Button
          variant="contained"
          className={classes.button}
          disabled={!item.can_withdraw}
          color="secondary"
          size="large"
          onClick={() =>
            account != 2 ? setVerificationPush(true) : history.push(href)
          }
        >
          {t("Withdraw")}
        </Button>
      </Box>
    </Box>
  );
};

export default Card;

const useStyles = makeStyles((theme) => ({
  price: {
    fontSize: 33,
    color: "#000000",
    textOverflow: "ellipsis",
    overflow: "hidden",
    whiteSpace: "nowrap",
  },

  div: {
    // boxShadow: "0px 2px 0px #E2E8F0",
    borderRadius: 8,
    // backgroundColor: "#fff",
    maxHeight: 162,
    padding: "24px 24px 16px",

    [theme.breakpoints.down(992)]: {
      maxHeight: 167,
      padding: "16px",
    },

    [theme.breakpoints.down(576)]: {
      maxHeight: 300,
      minHeight: 300,

      padding: "16px",
    },
  },

  grid: {
    [theme.breakpoints.down(576)]: {
      flexDirection: "column",
      justifyContent: "flex-start",
      alignItems: "flex-start",
    },
  },

  gridItem: {
    maxWidth: "50%",
    overflow: "hidden",

    [theme.breakpoints.down(576)]: {
      maxWidth: "100%",

      "&:last-child": {
        margin: "13px 0",
      },
    },
  },

  buttonBox: {
    whiteSpace: "nowrap",
    marginTop: 16,

    [theme.breakpoints.down(576)]: {
      display: "flex",
      flexDirection: "column",
      margin: 0,

      "& button": {
        width: "100%",
        marginBottom: 8,

        "&:last-child": {
          margin: 0,
        },
      },
    },
  },

  priceBox: {
    display: "flex",
    justifyContent: "flex-end",
    flexDirection: "column",
    alignItems: "center",
    [theme.breakpoints.down(576)]: {
      margin: "13px 0 16px",
      alignItems: "flex-start",
    },
  },

  button: {
    width: "50%",
    marginRight: 8,

    "&:last-child": {
      marginRight: 0,
    },
  },

  prefix: {
    // color: "#000000",
    fontWeight: "500",
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    overflow: "hidden",
  },

  name: {
    color: "#64748B",
    marginTop: 6,
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    overflow: "hidden",
  },
}));
