import React, { useEffect } from "react";
import { Typography } from "@mui/material";
import Card from "../Card";
import { useDispatch, useSelector } from "react-redux";
import Skeleton from "./Skeleton";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Navigation } from "swiper";
import "./index.css";
import { Box } from "@mui/system";
import { Link } from "react-router-dom";
import { MediumTitle } from "../../../../shared/Typography";
import { getProfileBalance } from "../../../../redux/reducers/myProfile";
import { makeStyles } from "@mui/styles";

SwiperCore.use([Navigation]);

const Wallets = ({ t, mode, setVerificationPush }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { profileBalance, isReloadProfileData, profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  useEffect(() => {
    dispatch(getProfileBalance());
    // eslint-disable-next-line
  }, []);

  const account = profileData?.data?.profile?.verification?.value;

  return (
    <>
      <Box
        display="flex"
        justifyContent="space-between"
        sx={{ flexDirection: { xs: "column", md: "row" } }}
      >
        <MediumTitle>{t("YourWallets")}</MediumTitle>
        <Link to="/dashboard/balances">
          <Box
            compontent={<Typography />}
            sx={{
              // color: "#191D23",
              fontSize: 23,
              marginBottom: " 16px",
              fontWeight: "500",
              textDecoration: "underline",
            }}
          >
            {t("SeeAll")}
          </Box>
        </Link>
      </Box>

      <Swiper
        mousewheel={true}
        className={classes.grid}
        slidesPerView={3}
        slidesPerGroup={3}
        spaceBetween={15}
        navigation={true}
        keyboard={{
          enabled: true,
        }}
        loop={false}
        grabCursor={true}
        breakpoints={{
          0: {
            slidesPerView: 1,
            slidesPerGroup: 1,
            spaceBetween: 0,
          },

          768: {
            slidesPerView: 2,
            slidesPerGroup: 2,
            spaceBetween: 16,
          },

          992: {
            slidesPerView: 2,
            slidesPerGroup: 2,
            spaceBetween: 16,
          },

          1200: {
            slidesPerView: 3,
            slidesPerGroup: 3,
            spaceBetween: 16,
          },
        }}
      >
        {!isReloadProfileData
          ? Array.from(Array(10).keys()).map((item, idx) => (
              <SwiperSlide key={idx}>
                <Skeleton style={{ width: 389 }} />
              </SwiperSlide>
            ))
          : Object.values(
              typeof profileBalance?.data?.currencies === "object" &&
                profileBalance?.data?.currencies
            )
              .sort((a, b) => b.balance - a.balance)
              ?.map((item, idx) => (
                <SwiperSlide key={idx}>
                  <Card
                    setVerificationPush={setVerificationPush}
                    mode={mode}
                    account={account}
                    item={item}
                    t={t}
                    href={`/dashboard/withdraw/${item.currency}`}
                    key={item.id}
                  />
                </SwiperSlide>
              ))}
      </Swiper>
    </>
  );
};

export default Wallets;

const useStyles = makeStyles((theme) => ({
  grid: {
    display: "flex",
    flexDirection: "row",

    [theme.breakpoints.down(992)]: {
      marginBottom: 24,
      paddingBottom: 24,
    },

    [theme.breakpoints.down(576)]: {
      marginBottom: 16,
      paddingBottom: 16,
    },

    "& .swiper-button-next": {
      zIndex: 10,
      position: "absolute",
      top: "50%",
      right: 0,
      transform: "translateY(-50%)",
      cursor: "pointer",
      width: "72px",
      height: "100%",
      // backgroundImage: "linear-gradient(to left, #f8fafd 0%,transparent 100%)",

      [theme.breakpoints.down(768)]: {
        backgroundImage: "none",
      },
    },

    "& .swiper-button-prev": {
      cursor: "pointer",
      width: 72,
      zIndex: 10,
      position: "absolute",
      top: "50%",
      left: 0,
      transform: "translateY(-50%)",
      height: "100%",
      // backgroundImage: "linear-gradient(to right, #f8fafd 0%,transparent 100%)",
      [theme.breakpoints.down(768)]: {
        backgroundImage: "none",
      },
    },
  },
}));
