import React from "react";
import {
  Typography,
  Box,
  Dialog,
  IconButton,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  FormControl,
  Button,
  FormHelperText,
} from "@mui/material";
import close from "../../assets/images/state/close.svg";
import { useState } from "react";
import { useFormik } from "formik";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getQuestions,
  getResults,
  sendAnswers,
} from "../../redux/reducers/questionsReducer";
import i18n from "../../i18nextInit";

const Questionnaire = ({ open, setOpen, t }) => {
  const dispatch = useDispatch();

  const { questions, results } = useSelector(
    ({ questionReducer }) => questionReducer
  );

  useEffect(() => {
    dispatch(getQuestions());
    dispatch(getResults());
  }, []);

  useEffect(() => {
    if (results?.response === "incomplete") setOpen(true);
  }, [results]);

  const [step, setStep] = useState(1);

  const formik = useFormik({
    initialValues: {},
    onSubmit: (values) => {
      const array = Object.entries(values);

      if (step < 5 && array.every((item) => item[1] === "")) {
        setStep((step) => step + 1);
      } else {
        const data = array.map((data) => ({
          question_id: data[0],
          answer: data[1],
        }));
        dispatch(sendAnswers(data));
        setStep((step) => step + 1);
      }
    },
  });

  return (
    <Dialog
      sx={{
        "& .MuiPaper-root": {
          p: "32px",
          minWidth: { xs: "unset", md: 570 },
          borderRadius: 2,
        },
      }}
      onClose={() => setOpen(true)}
      open={open}
    >
      {step === 6 && (
        <IconButton
          aria-label="close"
          sx={{ position: "absolute", top: 8, right: 8 }}
          onClick={() => setOpen(false)}
        >
          <img src={close} alt="" />
        </IconButton>
      )}

      {step !== 6 && (
        <>
          <Typography
            textAlign="center"
            mb={1}
            fontWeight="400"
            variant="body2"
          >
            {step} {t("of")} 5
          </Typography>
          <Box
            width="80%"
            borderRadius="4px"
            overflow="hidden"
            m="0 auto 16px"
            position="relative"
          >
            <Box height="5px" width="100%" backgroundColor="#ECECEC"></Box>
            <Box
              width={`${step * 20}%`}
              height="5px"
              position="absolute"
              top="0"
              sx={{ transition: "all 0.3s linear" }}
              backgroundColor="var(--primary)"
            ></Box>
          </Box>
          <Typography textAlign="center" fontWeight="600" variant="h3">
            {t("survey")}
          </Typography>
          <Typography
            variant="h5"
            fontWeight="600"
            // maxWidth="61%"
            textAlign="center"
            // m="0 auto"
          >
            {t("answerTheQuestions")}
          </Typography>
        </>
      )}
      <Box component="form" mt={2} onSubmit={formik.handleSubmit}>
        {questions?.questions?.map(
          (item, idx) =>
            step === item.number && (
              <FormControl
                fullWidth
                error={
                  i18n.language === "ru"
                    ? formik.values[item.question_id] !== ""
                    : formik.values[item.question_id] !== "Yes"
                }
              >
                <FormLabel sx={{ fontWeight: 300, color: "#000!important" }}>
                  {item.text}
                </FormLabel>
                <RadioGroup
                  name={item.question_id}
                  onChange={formik.handleChange}
                >
                  {item.answers.map((item) => (
                    <FormControlLabel
                      value={item}
                      control={<Radio required />}
                      label={item}
                    />
                  ))}
                </RadioGroup>
                {i18n.language === "ru" &&
                  formik.values[item.question_id] &&
                  formik.values[item.question_id] !== "" && (
                    <FormHelperText>{t("incorrect")}</FormHelperText>
                  )}
                {i18n.language === "en" &&
                  formik.values[item.question_id] &&
                  formik.values[item.question_id] !== "Yes" && (
                    <FormHelperText>{t("incorrect")}</FormHelperText>
                  )}
              </FormControl>
            )
        )}
        {step === 6 && (
          <>
            <center>
              <svg
                width="134"
                height="134"
                viewBox="0 0 134 134"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M66.9987 0.333008C30.2654 0.333008 0.332031 30.2664 0.332031 66.9997C0.332031 103.733 30.2654 133.666 66.9987 133.666C103.732 133.666 133.665 103.733 133.665 66.9997C133.665 30.2664 103.732 0.333008 66.9987 0.333008ZM98.8654 51.6664L61.0654 89.4664C60.132 90.3997 58.8654 90.933 57.532 90.933C56.1987 90.933 54.932 90.3997 53.9987 89.4664L35.132 70.5997C33.1987 68.6664 33.1987 65.4664 35.132 63.533C37.0654 61.5997 40.2654 61.5997 42.1987 63.533L57.532 78.8664L91.7987 44.5997C93.732 42.6664 96.932 42.6664 98.8654 44.5997C100.799 46.533 100.799 49.6664 98.8654 51.6664Z"
                  fill="#10B981"
                />
              </svg>
            </center>

            <Typography
              variant="h2"
              fontWeight={600}
              textAlign="center"
              mt={1}
              mb={1}
            >
                 ,  
            </Typography>
          </>
        )}
        {step === 6 ? (
          <Button
            sx={{ mt: 2 }}
            color="secondary"
            type="button"
            variant="contained"
            size="large"
            onClick={() => setOpen(false)}
            fullWidth
          >
            {t("ok")}
          </Button>
        ) : (
          <Button
            sx={{ mt: 2 }}
            color="primary"
            type="submit"
            disabled={
              i18n.language === "ru"
                ? Object.entries(formik.values).some(
                    (item) => item[1] === ""
                  )
                : Object.entries(formik.values).some((item) => item[1] === "No")
            }
            variant="contained"
            size="large"
            fullWidth
          >
            {t("further")}
          </Button>
        )}
      </Box>
    </Dialog>
  );
};

export default Questionnaire;
